:root {
    --primary: #ffcb22;
}

.btn {
    padding: 8px 20px;
    border-radius: 4px;
    outline: none;
    border: none;
    cursor: pointer;
}

.btn:hover {
    padding: 8px 20px;
    transition: all 0.25s ease-out;
    background: #fff;
    color: rgb(20, 100, 94);
    transition: 0.25s;
}

.btn--primary {
    background-color: var(--primary);

}

.btn--outline {
    background-color: transparent;
    color: #fff;
    padding: 8px 20px;
    border-radius: 4px;
    border: 1px solid var(--primary);
    transition: all 0.25s ease-out;
}

.btn--medium {
    padding: 8px 20px;
    border-radius: 4px;
    font-size: 18px;
    color: #fff;
}

.btn--large {
    padding: 12px 28px;
    border-radius: 4px;
    font-size: 20px;
    color: #fff;}